@use '../../stylesheets/mixins' as *;

.home {
  @include indents(0 20px 40px, 0);
  @include flex(column, flex-end, center);
  height: 95vh; // обязательно поправить после удаления ссылок в шапке
  text-align: center;

  &__header {
    @include indents();
    @include flex(column, $align-items: center, $gap: 8px);
  }

  &__links {
    @include flex(row, center, space-between, 8px);
  }

  &__link {
    text-decoration: none;
    color: var(--tg-theme-link-color);
  }

  &__button {
    @include button-style-reset();
    @include indents(8px 16px, 0);
    background: var(--tg-theme-button-color);
    // color: var(--tg-theme-button-text-color);
    border-radius: 25px;
  }

  &__title, &__text {
    @include indents();
    color: var(--tg-theme-text-color);
  }

  &__title {
    @include indents(20px, 0 auto);
  }

  &__text {
    @include indents(8px 8px 20px, 0 auto);
  }
}